:root{--main-font: "Roboto", sans-serif;--white-color: #fff;--orange-color: #FF6B08;--black-color: #000;--footer-backgraund-color: #F7F7F7;--red-color: #FF001B;--footer-text-color: #545454;--indent: 32px;--items: 1}body{font-family:"Roboto",sans-serif;font-size:12px;font-weight:500;line-height:24px;color:var(--black-color)}ul,p{padding:0;margin:0}h1,h2,h3,h4{margin:0}.list{list-style:none}.link{text-decoration:none}img{max-width:100%;display:block;height:auto}.container{margin:0 auto;padding:0 15px}@media screen and (min-width: 480px){.container{width:480px}}@media screen and (min-width: 768px){.container{width:768px}}@media screen and (min-width: 1200px){.container{width:1200px}}.section{padding-top:60px;padding-bottom:60px}@media screen and (min-width: 480px){.section{padding-top:20px;padding-bottom:20px}}.logo{font-weight:500;font-size:30px;line-height:1.1666666667;color:var(--white-color)}.btn{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin:0 auto;border:none;cursor:pointer;color:var(--white-color);background-color:var(--orange-color);font-weight:500;font-size:12px;line-height:16px;padding:13px 46px}@media screen and (min-width: 480px){.btn{padding:14px 36px}}.card-set{display:-webkit-box;display:-ms-flexbox;display:flex;gap:30px;-ms-flex-wrap:wrap;flex-wrap:wrap}.card-set-iteam{-ms-flex-preferred-size:calc((100% - var(--indent)*(var(--items) - 1))/var(--items));flex-basis:calc((100% - var(--indent)*(var(--items) - 1))/var(--items))}